import React, { Component } from 'react';
import '../../App.css';
let pos = 0;
export default class Greeting extends Component {
  constructor(props){
    super(props)
    this.state = {
      showLetters: '',
    }
    // this.greet = this.greeting.bind(this)
    // this.moveDown = this.moveDown.bind(this)
  }

  greeting = () =>{
    let br = 1;
    let atom = 2;
    let tab = 3;
    let txt = `${atom}Hello Visitor!${atom} Greet to see You!✌️✌️✌️`.split("");
    let interval = setInterval(() => {
      if(!txt[0]) return clearInterval(interval)
       this.setState((prevState)=> {
         return prevState.showLetters += txt.shift().replace(/[2]/gi, '⚛️')
      })
    }, 50);

    return ;
  }

  render(){
      return (
        <div id='greet'>
          <div className = "greeting text" >
            <p id="Text-show">{this.state.showLetters}</p>
          </div>
        </div>
      )
  }
  componentDidMount(){
    this.greeting()
    // window.addEventListener('scroll', this.moveDown);
  }
  componentWillUnmount = ()=> {
    clearInterval(this.greeting())
  }
}
